const certificates = () => {
    let certificates = [
        {
            url: "build/img/certificates/14.webp",
            id: "14",
        },
        {
            url: "build/img/certificates/13.webp",
            id: "13",
        },
        {
            url: "build/img/certificates/12.webp",
            id: "12",
        },
        {
            url: "build/img/certificates/11.webp",
            id: "11",
        },
        {
            url: "build/img/certificates/10.webp",
            id: "10",
        },
        {
            url: "build/img/certificates/9.webp",
            id: "9",
        },
        {
            url: "build/img/certificates/8.webp",
            id: "8",
        },
        {
            url: "build/img/certificates/7.webp",
            id: "7",
        },
        {
            url: "build/img/certificates/6.webp",
            id: "6",
        },
        {
            url: "build/img/certificates/5.webp",
            id: "5",
        },
        {
            url: "build/img/certificates/4.webp",
            id: "4",
        },
        {
            url: "build/img/certificates/3.webp",
            id: "3",
        },
        {
            url: "build/img/certificates/2.webp",
            id: "2",
        },
        {
            url: "build/img/certificates/1.webp",
            id: "1",
        },
    ]

    for (i in certificates) {
        img = certificates[i].url;
        id = certificates[i].id;

        const gallery = document.querySelector('.cert-bg')

        const image = document.createElement('IMG');
        image.src = img;
        image.setAttribute("alt", "Certificate");
        image.classList.add('certificate__img');
        image.dataset.imageId = id;

        image.onclick = showImage;

        const list = document.createElement('DIV');
        list.appendChild(image);
        list.classList.add('certificate')

        gallery.appendChild(list);
    }
}
certificates();


function showImage(e) { 
    const id = parseInt(e.target.dataset.imageId);
    
    // Generar la imagen
    const image = document.createElement('IMG');
    image.src = `build/img/certificatesQr/${id}.webp`;

    const overlay = document.createElement('DIV');
    overlay.appendChild(image);
    overlay.classList.add('overlay');
    image.classList.add('img-overlay');

    // Boton para cerrar img
    const closeImage = document.createElement('P');
    closeImage.textContent = 'X';
    closeImage.classList.add('closeBtn');

    // Al presionar, se cierra la imagen
    closeImage.onclick = function() {
        overlay.remove();
        body.classList.remove('fijar-body');
    }

    // Cuando se da click, cerrar la imagen
    overlay.onclick = function() {
        overlay.remove();
        body.classList.remove('fijar-body');
    }

    overlay.appendChild(closeImage);

    // Mostrar en el HTML
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');   
}